import { Component, OnInit } from '@angular/core';
import { AngularFireDatabase, AngularFireObject } from '@angular/fire/database';
import { Router, ActivatedRoute } from '@angular/router';
import { NgForm } from '@angular/forms';
import { ContentService } from '../core/content.service';
import { Tip } from '../core/tip';

@Component({
  selector: 'app-edit-tip',
  templateUrl: './edit-tip.component.html',
  styleUrls: ['./edit-tip.component.css']
})
export class EditTipComponent implements OnInit {

  public editedTip: Tip = new Tip();
  public tipID: string;
  private tipReference: AngularFireObject<any>;

  ngOnInit() { }

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private afDb: AngularFireDatabase,
    private contentService: ContentService
  ) {
    this.getTip();
  }

  getTip(): void {
    this.tipID = this.route.snapshot.paramMap.get('key');
    this.tipReference = this.afDb.object('tips/' + this.tipID);
    this.tipReference.snapshotChanges().subscribe(action => {
      this.editedTip = action.payload.val() as Tip;
    });
  }

  onSubmit(form: NgForm) {
    if (form.value.tid !== null) {
      this.contentService.updateTip(form.value);
    }
    this.router.navigate(['/consulting']);
  }

  onCancel() {
    this.router.navigate(['/consulting']);
  }
}
