import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Router } from '@angular/router';
import * as firebase from 'firebase/app';
import { ContentService } from '../core/content.service';

@Component({
  selector: 'app-new-tip',
  templateUrl: './new-tip.component.html',
  styleUrls: ['./new-tip.component.css']
})
export class NewTipComponent implements OnInit {

  public consultantID: string;

  ngOnInit() { }

  constructor(
    private router: Router,
    private contentService: ContentService
  ) {
    this.consultantID = firebase.auth().currentUser.uid;
  }

  onSubmit(form: NgForm) {
    this.contentService.insertTip(form.value);
    this.router.navigate(['/consulting']);
  }

  onCancel() {
    this.router.navigate(['/consulting']);
  }
}
