import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import * as firebase from 'firebase/app';
import { ContentService } from '../core/content.service';
import { Tip } from '../core/tip';

declare interface TableData {
  headerRow: string[];
}

@Component({
  selector: 'app-consulting',
  templateUrl: './consulting.component.html',
  styleUrls: ['./consulting.component.css']
})
export class ConsultingComponent implements OnInit {

  public tableData: TableData;
  public cosultantID: string;
  public tipsList: Tip[];

  constructor(
    private router: Router,
    private contentService: ContentService
  ) {
    this.cosultantID = firebase.auth().currentUser.uid;
  }

  ngOnInit() {
    this.tableData = {
      headerRow: ['ID', 'Title', 'Content', 'Tag', 'Date']
    };
    this.contentService.getTipsData().snapshotChanges().subscribe(item => {
      this.tipsList = [];
      item.forEach(element => {
        const y = element.payload.toJSON();
        y['tid'] = element.key;
        if (this.cosultantID === (y as Tip).cid) {
          this.tipsList.push(y as Tip);
        }
      });
    });
  }

  editTip(tip: Tip) {
    this.router.navigate(['/edit-tip/' + tip.tid]);
  }

  deleteTip(tip: Tip) {
    this.contentService.deleteTip(tip.tid);
  }

  addNewTip() {
    this.router.navigate(['/new-tip']);
  }

}
